<template>
    <div id="SideMenuItem" 
        
        :class="o_itemStyle"
        
        @pointerdown="OnPointerDownItem"
        
        @pointerup="OnPointerUpItem"
        
        ref="menuItem">
        
        <p>{{s_text}}</p>

    </div>
</template>

<script>

export default {
    name:"MenuItem",

    props:[
        "onclick"
    ],

    data(){
        return {

            /// public
            
            o_parent: null,

            o_children: [],

            /// private

            b_ifSelected: false,
            
            s_text:"default",
            
            f_callbackFunc:null,

            o_itemStyle:{
                
                item_normal: true,

                item_pressing: false,
            },

            o_menuItemDOM: null,
        }
    },

    methods:{

        // setter & getter
        SetText(s_text){
            this.s_text = s_text;
        },

        SetClickedCallBack(f_callbackFunc){
            this.f_callbackFunc = f_callbackFunc;
        },

        SetChildren(o_children){
            this.o_children= o_children;
        },

        // events

        OnPointerDownItem(){
            this.o_itemStyle.default = false;
            this.o_itemStyle.clicked = true;
        },

        OnPointerUpItem(){
            this.o_itemStyle.default = true;
            this.o_itemStyle.clicked = false;
            if(this.f_callbackFunc){
                this.f_callbackFunc();
            }
            this.$emit("onclicked", this);
        },

        // behaviours

        Focus(){
            this.b_ifSelected = true;

            this.o_itemStyle.default = false;
            this.o_itemStyle.clicked = true;

            if(this.f_callbackFunc){
                this.f_callbackFunc();
            }
        },

        Defocus(){
            this.b_ifSelected = false;
            this.o_itemStyle.default = true;
            this.o_itemStyle.clicked = false;
        },

    },

    mounted(){
        this.ifShowDropDownMenu = false;
        this.o_menuItemDOM = this.$refs.menuItem;
        this.$emit("onMenuItemMounted", this.o_menuItemDOM);
    },

    destroyed(){
        this.$emit("onMenuItemDesdroyed", this.o_menuItemDOM);
    },
}

</script>

<style lang="scss" scoped>

.base{
    margin: 10px 0;
    border: 10px black solid;
    text-align: center;
    font-size: 30px;
    padding: 10px;
    background-color: rgba($color: #ffffff, $alpha: 1.0);
}

.item_normal{
    @extend .base;
    // transition: all 0.5s ease;
}

.item_pressing{
    @extend .base;
    // border: 15px black solid;
    // background-color: rgba($color: #0a5cf5, $alpha: 1.0);
    // transition: all 0.5s ease;
}

</style>





